footer{
    height: 393px;
    padding-top: 53px;
    color: #fff;
}

.footer__block{
    padding: 42px 0 27px;

}
.footer__top{
    display: flex;
    align-items: start;
    justify-content: space-between;
    padding-bottom: 80px;
}

.footer__left{
    display: flex;
    justify-content: space-between;
    align-items: start;
}

.socials{
    width: 250px;
    padding-right: 90px;
}

.social__links{
    margin: 16px 0;
}

.web__links{
    display: flex;
    gap: 16px;
    margin-bottom: 8px;
}

.footer__title{
    font-size: 16px;
    font-weight: normal;
    color: #595959;
    line-height: 22px;
}
.web__links h2{
    font-size: 18px;
    font-weight: normal;
    padding-bottom: 4px;
    line-height: 24px;
    border-bottom: 1px solid #404040;
}

.web__links img{
    width: 24px;
    height: 24px;
}

.partner__links{
    display: flex;
    flex-direction: column;
    margin: 16px 0;
}

.partner__links a{
    width: fit-content;
    font-size: 18px;
    font-weight: normal;
    line-height: 24px;
    border-bottom: 1px solid #404040;
    padding-bottom: 4px;
    margin-bottom: 8px;
}

.footer__right{
    width: 367px;
    display: flex;
    flex-direction: column;
}

.footer__right h1{
    font-size: 18px;
    font-weight: normal;
    margin-bottom: 16px;
    line-height: 24px;
}

.inputs{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.inputs button{
    background: #0b2fd7;
    background-image: url(https://1shot.club/img/arrow-enter.svg);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: 24px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    height: 48px;
    width: 48px;
    margin-bottom: 20px;

}

.email{
    height: 48px;
    width: 296px;
    background: #050505;
    border: 1px solid #595959;
    border-radius: 4px;
    color: #b3b3b3;
    font-size: 16px;
    outline: 0;
    padding: 13px 12px 14px;
    margin-bottom: 20px;
}

.check{
    width: 20px;
    height: 20px;
}

.inputs1{
    display: flex;
    align-items: center;
}


.inputs1 p{
    font-size: 14px;
    padding-left: 9px;
}

.copyright__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.copyright__item{
    display: flex;
}

.copyright__item p{
    margin-right: 24px;
    font-size: 14px;
    font-weight: normal;
    line-height: 19px;
    color: #595959;
}

.app__block{
    display: flex;
    gap: 8px;
}

.button-appf{
    background: #050505;
    border: 1px solid #404040;
    border-radius: 4px;
    height: 34px;
    width: 104px;
    font-size: 14px;
    font-weight: normal;

}

.button-appf:hover{
    filter: brightness(2);
}

.ios1{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
}

.android1{
    width: 117px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
}


@media only screen and (max-width: 1000px) {
    .footer__top{
        display: flex;
        flex-direction: column;
        padding-bottom: 17px;
    }
    .copyright__footer{
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: start;
    }
    .copyright__item{
        display: flex;
        padding-bottom: 14px;
    }
    .footer__right{
        width: 100%;
    }
    .inputs input{
        width: 100%;
    }
    .inputs{
        display: flex;
        gap: 15px;
    }
}
@media only screen and (max-width: 767px) {
    .copyright__item{
        display: flex;
        flex-direction: column;
        padding-bottom: 14px;
    }
    .socials{
        width: fit-content;
        padding-right: 50px;
    }
}
@media only screen and (max-width: 320px) {
    .socials{
        width: fit-content;
        padding-right: 30px;
    }
}
@media only screen and (max-width: 320px) {
    .socials{
        width: fit-content;
        padding-right: 0px;
    }
    .footer__left{
        flex-direction: column;
    }
    .app__block{
        flex-direction: column;
    }
}