@font-face {
  font-family: ReSquare;
  src: url(../fonts/ReSquareMedium.ttf);
  src: url(../fonts/ReSquareRegular.ttf);
}
@import url(https://db.onlinewebfonts.com/c/acadfdfc97cd213de5a5609200dd5906?family=ALS+Hauss+Regular);

.navbar {
  min-height: 91px;
  max-height: 91px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 27px;
}

.nav__logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 100%;
  height: 40px;
  margin: 16px 0 12px;
  width: 151px;
  z-index: 999;
}

.select {
  width: 175px;
  height: 34px;
  appearance: none;
  background: none;
  background-image: url("https://1shot.club/img/icons/location-arrow-white.svg"),
    url("https://1shot.club/img/icons/arrow-down.svg");
  background-position: left 8px bottom 7px, right 8px bottom 7px;
  background-repeat: no-repeat;
  background-size: 16px, 16px;
  border: 1px solid #3f3f3f;
  border-radius: 4px;
  color: #6d6d6d;
  cursor: pointer;
  font-size: 14px;
  line-height: 18px;
  margin-left: 32px;
  outline: none;
  padding: 7px 29px 7px 34px;
  font-family: Arial, sans-serif;
}

.select:hover {
  background-image: url("https://1shot.club/img/icons/location-marker-blue.svg"),
    url("https://1shot.club/img/icons/arrow-down-blue.svg");
  border: 1px solid #2f54eb;
  color: #2f54eb;
}

option {
  font-family: Arial, sans-serif;
}

.nav__navigate {
  display: flex;
}

.nav__navigate ul {
  display: flex;
}
.nav__navigate ul li {
  display: flex;
  align-items: center;
  margin-left: 24px;
}

.nav__navigate ul li a {
  color: #bfbfbf;
  font-family: ReSquare, sans-serif;
  font-size: 20px;
  font-weight: 600;
  line-height: 120%;
}

.nav__navigate ul li a:hover {
  filter: brightness(2.5);
}

.booking__button {
  height: 32px;
  margin-left: 24px;
  border: 0;
  border-radius: 4px;
  background-color: #2f54eb;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-family: "ALS Hauss Regular";
  padding: 7px 8px;
}

.booking__button:hover {
  filter: brightness(1.3);
}

.nav__burger {
  display: none;
  background-color: #00000000;
}

.nav__burger span {
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  border: 0;
  outline: 0;
  border-radius: 2px;
}

@media only screen and (max-width: 1280px) {
  .logo {
    width: 100%;
  }
  .nav__navigate ul {
    display: none;
  }
  .select {
    display: none;
  }
  .nav__btn {
    width: fit-content;
    display: flex;
    align-items: center;
    gap: 20px;
  }
  .navbar {
    margin: 0 0px;
  }
  .nav__burger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 18px;
    width: 24px;
  }
}

@media only screen and (max-width: 767px) {
  .navbar {
    margin: 0 0px;
  }
}



.drawer{
  display: none;
}

@media only screen and (max-width: 1280px) {
  .drawer{
    display: flex;
  }
}


.media__nav {
  width: 100%;
  height: 100%;
  padding: 0 0 20px 0;
  background-color: #1d39c4;
  position: relative;
  align-items: center;
}

.med__logo {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 0 20px;
  padding-top: 20px;
}

.med__logo img {
  width: 100%;
}

.burger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  transform: rotate(45deg);
  position: relative;
}

.burger span {
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  border: 0;
  outline: 0;
  border-radius: 2px;
  position: absolute;
}

.span1 {
  transform: rotate(90deg);
}

.select1 {
  width: 100%;
  height: 54px;
  background: none;
  background-image: url("https://1shot.club/img/icons/location-arrow-white.svg");
  background-position: left 18px bottom 13px;
  background-repeat: no-repeat;
  background-size: 26px, 26px;
  border: 1px solid #5e66ab;
  border-left: 0;
  border-right: 0;
  color: #ffffff;
  font-size: 38px;
  font-weight: 600;
  line-height: 34px;
  padding: 0px 29px 7px 56px;
  font-family: ReSquare, sans-serif;
  margin: 30px 0;
  outline: none;
  cursor: pointer;
  appearance: none;
}

.select1 option{
  background-color: #1d39c4;
  font-size: 26px;
  font-weight: 600;
  font-family:ReSquare, serif;
}

.navigate ul li {
  list-style: none;
}

.navigate ul li a {
  color: #fff;
  display: block;
  font-family: ReSquare, sans-serif;
  font-size: 38px;
  font-weight: 600;
  line-height: 30px;
  padding: 5px 16px 11px;
  text-decoration: none;
  transition-duration: 0.25s;
}

.share{
  display: flex;
  padding: 15px 0;
}

.share1, .share2{
  background-position: 50%;
  background-repeat: no-repeat;
  border: 1px solid #fff;
  border-radius: 4px;
  height: 40px;
  margin-left: 16px;
  width: 40px;
}

.share1{
  background-image: url("https://1shot.club/img/vk.svg");
}
.share2{
  background-image: url("https://1shot.club/img/tiktok.svg");
}

.ios2{
  width: 130px;
  display: flex;
  align-items: center;
  gap: 8px;
  width: 150px;
  background-color: #00000000;
  padding: 3px 10px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  margin-left: 16px;
}
.android2{
  display: flex;
  align-items: center;
  gap: 12px;
  width: 150px;
  background-color: #00000000;
  padding: 3px 10px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
}